import React, {Component} from 'react';
import Navigation from "../common/Navigation";
import Footer from "../common/Footer";
import Header from "../common/Header";

import '../../styles/contact-us.css';
import ContactTransperant from "./ContactTransperant";

export default class ContactPage extends Component {

    //TODO....

    render = () => {
        return (
            <section>
                <Navigation {...this.props}/>
                <Header title="Contact Us" description='feel free to contact us'/>

               <ContactTransperant/>
                <div className="container-fluid">
                    <div className="container">
                        <div className="formBox">
                            <form>
                                <div className="row">
                                    <div className="col-sm-12">
                                        <h1>Contact form</h1>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="col-sm-6">
                                        <div className="inputBox ">
                                            <div className="inputText">First Name</div>
                                            <input type="text" name="" className="input"/>
                                        </div>
                                    </div>

                                    <div className="col-sm-6">
                                        <div className="inputBox">
                                            <div className="inputText">Last Name</div>
                                            <input type="text" name="" className="input"/>
                                        </div>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="col-sm-6">
                                        <div className="inputBox">
                                            <div className="inputText">Email</div>
                                            <input type="text" name="" className="input"/>
                                        </div>
                                    </div>

                                    <div className="col-sm-6">
                                        <div className="inputBox">
                                            <div className="inputText">Mobile</div>
                                            <input type="text" name="" className="input"/>
                                        </div>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="col-sm-12">
                                        <div className="inputBox">
                                            <div className="inputText">Email</div>
                                            <textarea className="input"></textarea>
                                        </div>
                                    </div>
                                </div>

                                <div className="row">
                                    <div className="col-sm-12">
                                        <input type="submit" name="" className="button" value="Send Message"/>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>


                <ContactTransperant/>
                <div className="container">
                    <div className="row">
                        <div className="col-md-8">
                            <div className="well well-sm">
                                <form>
                                    <div className="row">
                                        <div className="col-md-6">
                                            <div className="form-group">
                                                <label htmlFor="name">
                                                    Name</label>
                                                <input type="text" className="form-control" id="name" placeholder="Enter name" required="required" />
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="email">
                                                    Email Address</label>
                                                <div className="input-group">
                                <span className="input-group-addon"><span className="glyphicon glyphicon-envelope"></span></span>
                                                    <input type="email" className="form-control" id="email" placeholder="Enter email" required="required" /></div>
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="subject">
                                                    Subject</label>
                                                <select id="subject" name="subject" className="form-control" required="required">
                                                    <option value="na" selected="">Choose One:</option>
                                                    <option value="service">General Customer Service</option>
                                                    <option value="suggestions">Suggestions</option>
                                                    <option value="product">Product Support</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div className="col-md-6">
                                            <div className="form-group">
                                                <label htmlFor="name">
                                                    Message</label>
                                                <textarea name="message" id="message" className="form-control" rows="9" cols="25" required="required"
                                                          placeholder="Message"></textarea>
                                            </div>
                                        </div>
                                        <div className="col-md-12">
                                            <button type="submit" className="btn btn-primary pull-right" id="btnContactUs">
                                                Send Message</button>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                        <div className="col-md-4">
                            <form>
                                <legend><span className="glyphicon glyphicon-globe"></span> Our office</legend>
                                <address>
                                    <strong>Twitter, Inc.</strong><br/>
                                    795 Folsom Ave, Suite 600<br/>
                                    San Francisco, CA 94107<br/>
                                    <abbr title="Phone">
                                        P:</abbr>
                                    (123) 456-7890
                                </address>
                                <address>
                                    <strong>Full Name</strong><br/>
                                    <a href="mailto:#">first.last@example.com</a>
                                </address>
                            </form>
                        </div>
                    </div>
                </div><br/><br/>


                <Footer/>
            </section>
        )
    }
}